.banner{
    height: 90vh;
    object-fit: contain;
   
    color: white;
}

.banner_content{
    padding-top: 350px;
    margin-left: 30px;
    height: 190px;
  
}
.banner_title{
    font-weight: 1000;
    font-size: 3rem;
    padding-bottom: 0.3rem;
}

.banner_button{
    border: none;
    outline: none;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: rgba(51,51,51,0.5);
    margin-right: 1rem;
    cursor: pointer;
    color: white;
    font-weight: 700;
    padding-right: 3.5rem;
    padding-left: 3.5rem;
