.row {
  color: white;
  height: 100%;
}

.row_posters {
  display: -webkit-flex;
  display: flex;
  margin-left: 70px;
 
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  scroll-behavior: smooth;
  position: relative;
}
.row_title {
  padding-top: 20px;
  padding-left: 90px;
  font-size: 30px;
}
.row_posters::-webkit-scrollbar {
  display: none;
}

.row_poster {
  display: -webkit-flex;
  display: flex;
  width: 200px;
  margin: 20px 5px;
  border-radius: 4px;
  object-fit: contain;
  transition: 1s;
}



.poster {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  cursor: pointer;
  position: relative;
  transition: all .5s;

}

.poster .overlay {
  color: white;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
 transition: 0.5s;
 background-color: rgba(17, 17, 17, 0.5);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 20px;
  overflow: hidden;
  position: absolute;
  top: 100%;
  left: 0%;
  
  z-index: 1;
}

.row_poster:hover{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  
}


.poster:hover .overlay{
    top: 0;
 
}


.text{
    text-align: center;
    margin: 0px 10px;
}

.para{
margin: 7px;
font-size: 15px;
text-align: center;
}




.scroll:hover,
.scrollRight:hover,
.scrollLarge:hover,
.scrollRightLarge:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
.row:hover .scroll {
  visibility: visible;
  scroll-behavior: smooth;
  background-color: rgba(17, 17, 17, 0.1);
}

.scroll {
  position: relative;
  float: right;
  top: -230px;
  margin-right: 10px;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 30px;
  cursor: pointer;
  visibility: hidden;
  z-index: 2;
}


.row:hover .scrollRight{
  visibility: visible;
  background-color: rgba(17, 17, 17, 0.3);
}



.scrollRight{
  position: relative;
  top: -230px;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 30px;
  cursor: pointer;
  margin-left: 15px;
  visibility: hidden;
}




